@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,700italic,900);
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }
input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

* { box-sizing: border-box; }
*:before, *:after { box-sizing: border-box; }

.icon { display: inline-block; }
.icon-logo { background: url("../img/svg/logo.svg") no-repeat 0 0; background-size: 175px 46px; width: 175px; height: 46px; display: inline-block; }
.icon-stock { background-image: url("../img/icons.png"); width: 122px; height: 100px; background-position: 0px 0px; }
.icon-google-play { background: url("../img/svg/google-play.svg") no-repeat 0 0; background-size: 17px 18px; width: 17px; height: 18px; display: inline-block; }
.icon-f-app { background: url("../img/svg/f-app.svg") no-repeat 0 0; background-size: 23px 23px; width: 23px; height: 23px; display: inline-block; }
.icon-pci { background: url("../img/svg/pci.svg") no-repeat 0 0; background-size: 153px 42px; width: 153px; height: 42px; display: inline-block; }
.icon-secure { background: url("../img/svg/secure.svg") no-repeat 0 0; background-size: 136px 40px; width: 136px; height: 40px; display: inline-block; }
.icon-mc-maney-send { background: url("../img/svg/mc-maney-send.svg") no-repeat 0 0; background-size: 147px 40px; width: 147px; height: 40px; display: inline-block; }
.icon-verified-visa { background: url("../img/svg/verified-visa.svg") no-repeat 0 0; background-size: 71px 40px; width: 71px; height: 40px; display: inline-block; }
.icon-mc-securecode { background: url("../img/svg/mc-securecode.svg") no-repeat 0 0; background-size: 117px 40px; width: 117px; height: 40px; display: inline-block; }
.icon-visa { background: url("../img/svg/visa.svg") no-repeat 0 0; background-size: 72px 23px; width: 72px; height: 23px; display: inline-block; }
.icon-mc { background: url("../img/svg/mc.svg") no-repeat 0 0; background-size: 75px 40px; width: 75px; height: 40px; display: inline-block; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white slick-image-url("ajax-loader.gif") center center no-repeat; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -14px; font: 0/0 a; text-shadow: none; color: transparent; height: 50px; background: none; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 50px; line-height: 50px; }

.slick-prev { left: 0; }
.slick-prev:before { content: "←"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-next { right: 0; }
.slick-next:before { content: "→"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { opacity: 0.3; box-shadow: none; }
.slick-dots li:before { display: none; }
.slick-dots button { display: block; width: 6px; height: 6px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }
.slick-dots button:hover { box-shadow: none; }

body { font-family: "Roboto", sans-serif; font-weight: 400; font-size: 14px; line-height: 1.3; }

a { text-decoration: none; }

h1, h2, h3, h4, h5, h6, em, b, strong, i, th, td { font-weight: normal; }

.in, .header__in, .slider__in { padding: 0 10px; margin: 0 auto; max-width: 960px; *zoom: 1; }
.in:after, .header__in:after, .slider__in:after { content: " "; display: table; clear: both; }

.header { position: relative; height: 62px; color: #fff; font-size: 12px; z-index: 1; box-shadow: inset 0 -1px 0 #3498db; border-bottom: 1px solid #21618c; background: #2a7cb3; }
.header__right { float: right; margin-top: 15px; *zoom: 1; }
.header__right:after { content: " "; display: table; clear: both; }

.logo { position: relative; float: left; margin-top: 11px; }
.logo:hover .hint { opacity: 1; visibility: visible; z-index: 1; }
.logo a { color: #fff; *zoom: 1; }
.logo a:after { content: " "; display: table; clear: both; }
.logo .icon { float: left; margin-right: 5px; }
.logo__text { float: left; margin-top: 6px; width: 105px; }

.timer { position: absolute; left: 50%; transform: translate(-50%, 0); margin-left: 1%; margin-top: 15px; font-size: 22px; font-family: "Roboto", sans-serif; font-weight: 700; *zoom: 1; }
.timer:hover .hint { opacity: 1; visibility: visible; z-index: 1; }
.timer:after { content: " "; display: table; clear: both; }
.timer__wrap, .timer__numb, .timer__thous, .timer__text { float: left; }
.timer__thous { margin-left: 2px; padding-top: 9px; line-height: 1; }
.timer__numb { position: relative; margin-left: 2px; height: 29px; line-height: 27px; border: 1px solid #3497da; box-shadow: inset 0 1px 0 #1c5e89, 0 1px 0 #1c5e89; border-radius: 3px; }
.timer__numb:before, .timer__numb:after { position: absolute; content: ""; left: 0; right: 0; height: 1px; z-index: 2; }
.timer__numb:before { top: 11px; background: rgba(33, 97, 129, 0.7); }
.timer__numb:after { top: 12px; background: rgba(255, 255, 255, 0.5); }
.timer__numb:first-child { margin-left: 0; }
.timer__numb span { position: relative; padding: 0 6px; z-index: 1; display: block; }
.timer__numb span:before { position: absolute; content: ""; top: 0; left: 0; right: 0; bottom: 0; opacity: 0.1; background: radial-gradient(50% 50%, circle farthest-corner, rgba(255, 255, 255, 0) 0%, black 100%); }
.timer__text { margin-left: 9px; width: 95px; font-size: 12px; font-family: "Roboto", sans-serif; font-weight: 400; }
.timer .hint { margin-top: 11px; }

.hint { position: absolute; top: 100%; left: 50%; transform: translate(-26%, -4px); font-size: 11px; white-space: nowrap; visibility: hidden; z-index: -1; opacity: 0; transition: opacity 0.2s, visibility 0.2s; }
.hint span { padding: 8px 9px; color: #2A7CB2; border-radius: 2px; box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3); background: #fff; display: inline-block; }

.cont { float: left; }
.cont + .lang { margin-left: 20px; }
.cont .fa { margin-right: 8px; font-size: 32px; }
.cont__right { float: right; margin-top: 2px; }
.cont__right a { margin-top: 1px; color: #fff; line-height: 1.2; transition: border-color 0.2s; border-bottom: 1px solid transparent; display: block; }
.cont__right a:hover { border-color: #fff; }
.cont__right a:first-child { margin-top: 0; }

.lang { float: right; *zoom: 1; }
.lang:after { content: " "; display: table; clear: both; }
.lang__item { float: left; margin-left: 5px; }
.lang__item:first-child { margin-left: 0; }
.lang__item.is-active a { color: #2A7CB2; }
.lang__item.is-active a:before { transform: scale(1); opacity: 1; }
.lang a { position: relative; height: 32px; width: 32px; color: #fff; line-height: 30px; text-align: center; text-transform: uppercase; border-radius: 50%; transition: color 0.3s, box-shadow 0.3s; border: 1px solid #fff; display: block; }
.lang a:hover { color: #2A7CB2; box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3); }
.lang a:hover:before { transform: scale(1); opacity: 1; }
.lang a:before { position: absolute; content: ""; top: -1px; left: -1px; right: -1px; bottom: -1px; background: #fff; transition: transform 0.3s, opacity 0.3s; transform: translate3d(0, 0, 0); transform: scale(0); will-change: opacity; opacity: 0; border-radius: 50%; }
.lang a span { position: relative; z-index: 1; }

.slider { box-shadow: inset 0 -1px 0 #2a7cb3; border-bottom: 1px solid #21618c; background: url(../img/cloud.png) no-repeat 65% 10px, #2a7cb3; }
.slider__slide { width: 100%; }
.slider .slick-dots { right: 125px; bottom: 16px; text-align: right; font-size: 0; line-height: 1; }
.slider .slick-dots li { margin-left: 7px; padding: 0; }
.slider .slick-dots li:first-child { margin-left: 0; }
.slider .slick-dots li.slick-active button { opacity: 1; }
.slider .slick-dots li.slick-active button:before { transform: scale(1); opacity: 1; }
.slider .slick-dots button { position: relative; height: 13px; width: 13px; border: 1px solid #fff; background: none; }
.slider .slick-dots button:before { position: absolute; content: ""; top: 2px; left: 2px; height: 7px; width: 7px; border-radius: 50%; background: #fff; will-change: opacity; transition: background 0.3s, transform 0.3s, opacity 0.3s; transform: scale(0); opacity: 0; }

.stock { padding: 10px 0 10px 55px; text-align: center; color: #fff; font-family: "Roboto", sans-serif; font-weight: 900; }
.stock__text { margin-top: 45px; text-transform: uppercase; font-size: 13px; text-shadow: 0px 0.82px 1.64px rgba(0, 0, 0, 0.2); vertical-align: top; display: inline-block; }
.stock__text span.is-up { font-size: 22px; }
.stock__text span + span { margin-left: 8px; }
.stock__disc { position: relative; margin-left: 5px; vertical-align: top; display: inline-block; }
.stock__plus { position: absolute; top: 100%; left: 50%; transform: translate(-68%, -9px); }
.stock__more { text-align: right; margin-top: -10px; }

.btn { position: relative; padding: 5px 20px; color: #fff; font-family: "Roboto", sans-serif; font-weight: 400; font-size: 12px; line-height: 1.1; transition: color 0.2s, box-shadow 0.2s, background 0.2s; border: 1px solid #fff; border-radius: 25px; display: inline-block; }
.btn:hover { color: #2A7CB2; box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3); background: #fff; }

button.btn { line-height: 1; }


